.pum-field-checkbox {
  position: relative;

  label {

    margin-left: 1.5em;

    &.pum-desc {
      display: inline;
      font-weight: inherit;
      font-size: inherit;
      margin: 0 0 1em;
    }
  }

  input[type="checkbox"] {
    position: absolute;
    top: .25em;
  }

}

.pum-field-multicheck {

  input + label {
    font-weight: normal;
    display: inline-block;
  }

  label:first-child {
    font-weight: bold;
    margin: 0 0 .25em;
  }

  > p.pum-desc {
    margin: 0 0 .5em;
  }

}